<template>
  <div class="d-flex flex-column align-items-center">
    <br />
    <h1 class="my-5">Edici칩n de usuario 游</h1>

    <form
      class="d-flex flex-column"
      @submit.prevent="actualizarUsuario(usuario)"
      enctype="multipart/form-data"
    >
      <div id="form" class="container px-5 pb-3">
        <h3 class="mt-3">Usuario</h3>
        <input
          type="text"
          class="form-control my-2"
          placeholder="Nombre"
          v-model="usuario.nombre"
          required
        />
        <div class="d-flex justify-content-center mt-3" v-if="usuario.admin">
          <p>Tipo de usuario:</p>
          <div class="form-check mx-5">
            <input
              class="form-check-input"
              type="radio"
              name="flexRadioDefault"
              id="tatuador"
              value="true"
              v-model="usuario.tatuador"
              required
            />
            <label class="form-check-label" for="tatuador">Tatuador</label>
          </div>
          <div class="form-check">
            <input
              class="form-check-input"
              type="radio"
              name="flexRadioDefault"
              id="cliente"
              value="false"
              checked
              v-model="usuario.tatuador"
              required
            />
            <label class="form-check-label" for="cliente">Cliente</label>
          </div>
        </div>
        <div class="d-flex">
          <label class="" for="date">Fecha de nacimiento:</label>
          <input
            type="date"
            id="date"
            class="form-control my-2"
            v-model="usuario.fechaNacimiento"
            required
          />
        </div>
        <div class="d-flex align-items-center">
          Foto de perfil:
          <input type="file" class="form-control m-2" @change="agregarFile" />
        </div>
        <div class="input-group mb-2">
          <div class="input-group-prepend">
            <span id="apend" class="input-group-text my-2">@</span>
          </div>
          <input
            type="text"
            class="form-control my-2"
            id="inlineFormInputGroup"
            placeholder="Username"
            v-model="usuario.usuario"
            required
          />
        </div>
        <div>
          <b-form  @submit.stop.prevent>
            <b-form-input v-if="!(accion)" v-model="usuario.password" 
              type="password" placeholder="Contrase침a" 
              :state="validation" id="feedback-user">
            </b-form-input>
            <b-form-input v-else v-model="usuario.password" 
              type="password" placeholder="Contrase침a" 
              id="feedback-user">
            </b-form-input>

            <b-form-invalid-feedback v-if="!(accion)" :state="validation">
              Debe tener entre 8 y 16 caracteres, al menos un d칤gito, al menos una min칰scula y al menos una may칰scula.
            </b-form-invalid-feedback>
            <b-form-valid-feedback v-if="!(accion)" :state="validation">
              ok.
            </b-form-valid-feedback>
          </b-form>
        </div>
      </div>

      <b-button id="enviar" pill class="my-4" type="submit">Editar</b-button>
    </form>

    <div class="container">
      <b-alert
        :show="dismissCountDown"
        :variant="mensaje.color"
        @dismissed="dismissCountDown = 0"
        @dismiss-count-down="countDownChanged"
      >
        <p>{{ mensaje.texto }}</p>
        <b-progress
          :variant="mensaje.color"
          :max="dismissSecs"
          :value="dismissCountDown"
          height="4px"
        ></b-progress>
      </b-alert>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      usuarios: [],
      dismissSecs: 5,
      dismissCountDown: 0,
      showDismissibleAlert: false,
      mensaje: {
        color: '',
        texto: '',
      },
      usuario: {
        nombre: '',
        tatuador: Boolean,
        fechaNacimiento: '',
        fotoPerfil: '',
        usuario: '',
        password: '',
        aboutYou: '',
        archivo: null,
        acceso: false,
        admin: false,
      },
      nickname: '',
      accion: false,
      valida: '',
    }
  },
  created() {
     if (localStorage.getItem('tatuador') == 'true') {
      this.accion = true
    }
    var enlace = location.href.split('?id=')[1]
    if (enlace == undefined ||
      (localStorage.getItem('tatuador') != 'true' &&
        enlace != localStorage.getItem('id'))) {
      this.$router.go(-1)
    } else {
      if (localStorage.getItem('tatuador') == 'true') {
        this.usuario.acceso = true
        this.usuario.admin = true
      }
      const id = location.href.split('?id=')[1]
      this.axios
        .get('usuarios/listar/' + id)
        .then((res) => {
          this.usuario = res.data
          this.nickname = res.data.usuario
          this.usuario.fechaNacimiento = res.data.fechaNacimiento.substring(
            0,
            10,
          )
        })
        .catch((e) => {
          console.log(e.response)
        })
    }
  },
  computed: {
      validation() {
         var regexp = /^(?=\w*\d)(?=\w*[A-Z])(?=\w*[a-z])\S{8,16}$/
        if (regexp.test(this.usuario.password)) {
          this.valida = true
        }else{
          this.valida = false
        }
        return regexp.test(this.usuario.password)
      }
    },
  methods: {
    alertaArchivoNulo() {
      this.mensaje.color = 'warning'
      this.mensaje.texto = 'La foto debe ser en formato .jpg .png o .jpeg'
      this.showAlert()
    },
    alertaTrue() {
      this.mensaje.color = 'primary'
      this.mensaje.texto = 'Usuario editado con exito! 游녧'
      this.showAlert()
    },
    alertaFalse() {
      this.mensaje.color = 'warning'
      this.mensaje.texto = 'Error :('
      this.showAlert()
    },
    alertaUsuarioExistente() {
      alert('Nombre de usuario ya existente')
    },
    actualizarUsuario(item) {
      if ((this.valida == true && this.accion == false) || this.accion == true) {
        this.axios
          .post('/usuarios/comprobarUsuario', this.usuario)
          .then((res) => {
            if (res.data.existe == false || this.nickname == item.usuario) {
              if (this.usuario.archivo != undefined) {
                this.axios
                  .put('usuarios/eliminarFoto/' + item._id, item)
                  .then((res) => {})
                  .catch((e) => {
                    this.alertaFalse()
                  })
                  .finally((e) => {
                    const formdata = new FormData()
                    formdata.append('fotoPerfil', this.usuario.archivo)
                    this.axios
                      .post('/usuarios/agregarFoto', formdata)
                      .then((res) => {
                        this.usuario.fotoPerfil = res.data
                      })
                      .catch((e) => {
                        this.alertaFalse()
                      })
                      .finally((e) => {
                        this.axios
                          .put('usuarios/actualizar/' + item._id, item)
                          .then((res) => {
                              this.usuario.nombre = res.data.nombre
                              this.usuario.tatuador = res.data.tatuador
                              this.usuario.fechaNacimiento = res.data.fechaNacimiento
                              this.usuario.usuario = res.data.usuario
                              this.usuario.password = res.data.password
                              this.usuario.aboutYou = res.data.aboutYou
                              this.usuario.fotoPerfil = res.data
                              this.alertaTrue()
                          })
                          .catch((e) => {
                            this.alertaFalse()
                          })
                      })
                  })
              } else {
                this.axios
                  .put('usuarios/actualizar/' + item._id, item)
                  .then((res) => {
                    if ((this.valida == true && this.accion == false) || this.accion == true) {
                      this.usuario.nombre = res.data.nombre
                      this.usuario.tatuador = res.data.tatuador
                      this.usuario.fechaNacimiento = res.data.fechaNacimiento
                      this.usuario.usuario = res.data.usuario
                      this.usuario.password = res.data.password
                      this.usuario.aboutYou = res.data.aboutYou
                      this.alertaTrue()
                    }else{
                      this.alertaFalse()
                    }
                  })
                  .catch((e) => {
                    console.log(e)
                  })
              }
            } else {
              this.alertaUsuarioExistente()
            }
          })
      }else{
        this.alertaFalse()
      }

    },
    agregarFile(e) {
      var file = e.target.files[0]
      const types = ['image/jpg', 'image/jpeg', 'image/png']
      if (types.includes(file.type)) {
        this.usuario.archivo = file
      } else {
        this.alertaArchivoNulo()
        e.target.value = ''
      }
    },
    countDownChanged(dismissCountDown) {
      this.dismissCountDown = dismissCountDown
      if (this.mensaje.color == 'primary') {
        setTimeout(() => {
          this.$router.go(-1)
        }, 5000)
      }
    },
    showAlert() {
      this.dismissCountDown = this.dismissSecs
    },
  },
}
</script>

<style>
#form {
  background-color: #82a5c7;
  color: #243241;
  border-radius: 10px;
}
input {
  background-color: #4e647a !important;
  color: #82a5c7 !important;
}
span#apend {
  background-color: #243241 !important;
  color: #82a5c7 !important;
  border-top-right-radius: 0;
  border-bottom-right-radius: 0;
}
#enviar {
  background-color: #82a5c7;
  color: #243241;
}
</style>
